.search-icon {
  width: 24px;
  position: relative;
  height: 24px;
  overflow: hidden;
  flex-shrink: 0;
}
.search-on-progress-line1 {
  width: 2px;
  position: relative;
  max-width: 100%;
  overflow: hidden;
  height: 16px;
}
.search-bar1,
.search-stocks {
  display: flex;
  align-items: center;
}
.search-stocks {
  width: 271px;
  position: relative;
  height: 19px;
  flex-shrink: 0;
}
.search-bar1 {
  align-self: stretch;
  border-radius: 14px;
  background-color: #fff;
  overflow: hidden;
  flex-direction: row;
  justify-content: flex-start;
  padding: 10px 14px;
  gap: 16px;
}
.material-symbols-lightsearch-icon {
  width: 18px;
  position: relative;
  height: 18px;
  overflow: hidden;
  flex-shrink: 0;
}
.search-result {
  align-self: stretch;
  height: 16px;
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: flex-start;
  padding: 0 20px;
  box-sizing: border-box;
  gap: 16px;
}
.bank-nifty {
  position: relative;
}
.search-result-parent {
  align-self: stretch;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-end;
  gap: 10px;
  color: #000;
}
.search-bar2,
.statedefault {
  background-color: #fff;
  display: flex;
  box-sizing: border-box;
  gap: 16px;
}
.statedefault {
  position: absolute;
  top: 20px;
  left: 20px;
  border-radius: 10px;
  width: 400px;
  height: 192px;
  overflow: hidden;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-end;
  padding: 6px 10px;
}
.search-bar2 {
  align-self: stretch;
  border-radius: 14px;
  height: 44px;
  flex-shrink: 0;
  flex-direction: row;
  align-items: center;
  justify-content: flex-start;
  padding: 10px 14px;
}
.search-bar2,
.search-result5 {
  overflow: hidden;
}
.search-result5,
.search-result6 {
  align-self: stretch;
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: flex-start;
  padding: 0 20px;
  gap: 16px;
}
.search-result-group,
.statesearch-result {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}
.search-result-group {
  align-self: stretch;
  height: 122px;
  justify-content: flex-end;
  gap: 8px;
  color: #000;
}
.statesearch-result {
  position: absolute;
  top: 232px;
  left: 20px;
  border-radius: 10px;
  background-color: #fff;
  width: 400px;
  height: 53px;
  overflow: hidden;
  justify-content: flex-start;
  gap: 9px;
}
.search-bar {
  width: 100%;
  border-radius: 5px;
  border: 1px dashed #9747ff;
  box-sizing: border-box;
  height: 444px;
  overflow: hidden;
  text-align: left;
  font-size: 12px;
  color: #5f5959;
  font-family: Fraunces;
}
